.backdrop {
  z-index: 20;
  opacity: .25;
  visibility: visible;
  width: 100%;
  height: 100%;
  background-color: #000;
  transition: opacity .3s cubic-bezier(.175, .885, .32, 1.275), visibility .3s cubic-bezier(.175, .885, .32, 1.275);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  z-index: 20;
  opacity: 1;
  visibility: visible;
  width: 280px;
  max-height: 96%;
  background-color: #fff;
  padding: 48px 20px 40px;
  transition: opacity .3s cubic-bezier(.175, .885, .32, 1.275), visibility .3s cubic-bezier(.175, .885, .32, 1.275);
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 704px;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 806px;
  }
}

.modal.modal-open {
  overflow: hidden;
}

.modal::-webkit-scrollbar {
  display: none;
}

.backdrop.is-hidden, .modal.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  overflow: hidden;
}

.close-btn {
  color: #1d0656;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  text-decoration: none;
  transition: transform .5s;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.close-btn:hover {
  fill: #1d0656;
  opacity: 1;
  border: 1px solid rgba(62, 74, 144, .3);
  border-radius: 100%;
  transform: rotate(360deg);
  box-shadow: 0 0 1px 5px rgba(82, 33, 117, .4), 0 0 1px 10px rgba(138, 59, 88, .1), 0 0 1px 15px rgba(111, 147, 210, .1);
}

.close-btn:active {
  transform: scale(.9);
}

.close-btn__icon {
  fill: currentColor;
  display: block;
}

