}

.social-list__link {
  width: 35px;
  height: 35px;
  fill: var(--secondary-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.social-list__link:hover, .social-list__link:focus {
  background-color: var(--secondary-color);
  fill: var(--main-color);
}

.switch {
  width: 50px;
  height: 28px;
  display: inline-block;
  position: fixed;
  top: 36px;
  right: 20px;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: var(--main-color);
  border: 1px solid var(--secondary-color);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: var(--header-color);
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--main-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX(24px);
  -ms-transform: translateX(24px);
  transform: translateX(22px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

input:checked + .slider.round:before {
  background-color: var(--secondary-color);
}

.dark-theme {
  background-color: var(--main-color);
  color: var(--header-color);
}

@media screen and (max-width: 468px) {
  .slider:before {
    height: 15px;
    width: 15px;
  }

  .switch {
    width: 40px;
    height: 23px;
    top: 10px;
  }

  input:checked + .slider:before {
    transform: translateX(17px);
  }
}

.spinner__overlay {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .25);
  transition: opacity .15s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner__overlay.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.spinner--on {
  width: 100px;
  height: 100px;
  z-index: 999;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.spinner--on div {
  box-sizing: border-box;
  width: 45px;
  height: 45px;
  border: 8px solid #fa6921;
  border-color: #fa6921 #101316;
  border-radius: 50%;
  margin: 8px;
  animation: spinner 2s cubic-bezier(.5, 0, .5, 1) infinite;
  display: block;
  position: absolute;
}

.darkmode--activated .spinner--on div {
  border-left: 8px solid #f7f7f7;
  border-right: 8px solid #f7f7f7;
}

.spinner--on div:nth-child(1) {
  animation-delay: -450ms;
}

.spinner--on div:nth-child(2) {
  animation-delay: -300ms;
}

.spinner--on div:nth-child(3) {
  animation-delay: -150ms;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

